import React from 'react';
import { Table, Button } from 'react-bootstrap';
import "../cart.scss";
import { addCount, sortName, miCount } from "../store";
import { useDispatch, useSelector } from 'react-redux';

const Cart = () => {
    let state = useSelector((state) => state)
    let a = useSelector((state) => { return state })
    let dispatch = useDispatch();

    function total() {
        let sum = 0;
        for (let v in state.cart) {
            sum += state.cart[v].price * state.cart[v].count
        }
        return sum
    }

    let min = { value: 1 };
    return (

        <div className='wrap'>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>가격</th>
                        <th>수량</th>
                        <th>변경하기</th>
                    </tr>
                </thead>

                <tbody>
                    {
                        state.cart.map((v, i) =>
                            <tr key={i}>

                                <td>{state.cart[i].id}</td>
                                <td>{state.cart[i].name}</td>
                                <td>{state.cart[i].price}</td>
                                <td>{state.cart[i].count}</td>
                                <td>
                                    <button onClick={() =>
                                        dispatch(addCount(state.cart[i].name))
                                    }>+</button>
                                    <button onClick={() =>
                                        dispatch(miCount(state.cart[i].name))
                                    }>-</button>
                                </td>
                            </tr>

                        )
                    }
                </tbody>
            </Table>

            <div className='total'>
                <div className='total_inner'>
                    <div className='total_sum'>
                        <p>총 결제금액 : {total().toLocaleString()}원</p>
                    </div>

                    {/* <div className='btn_area'>
                        <Button variant="outline-primary" onClick={() => {
                            dispatch(sortName(state.cart.Name))
                        }}>이름순정렬
                        </Button>{' '}
                    </div> */}
                </div>
            </div>

        </div>
    );
};

export default Cart;

