import React from 'react';
import instar from '../data/instar';
import { BrowserRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import { Carousel } from 'react-bootstrap';
import { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css/scrollbar";
import { Scrollbar } from "swiper";
import {
    MDBTabs,
    MDBTabsItem,
    MDBTabsLink,
    MDBTabsContent,
    MDBTabsPane,
    MDBNavbarLink
} from 'mdb-react-ui-kit';


const Home = (props) => {

    // let [mens] = useState(men);    
    // let [womens] = useState(women);
    // let [couples] = useState(couple);
    let [instargrams] = useState(instar);
    const [basicActive, setBasicActive] = useState('tab1');
    let [res, setRes] = useState([0, 1, 2, 3, 4, 5, 6, 7, 8]);
    const handleBasicClick = (value) => {
        if (value === basicActive) {
            return;
        }
        setBasicActive(value);
    };

    let navigate = useNavigate();

    return (
        <div>
            <div className='body_wrap'>

                <div className='swiper_zone'>
                    <Carousel>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                alt="Second slide"
                                src={process.env.PUBLIC_URL + './img/slide1.jpg'}
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                          
                                alt="Third slide"
                                src={process.env.PUBLIC_URL + './img/slide2.jpg'}
                            />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                                className="d-block w-100"
                                alt="Third slide"
                                src={process.env.PUBLIC_URL + './img/slide3.jpg'}
                            />
                        </Carousel.Item>
                    </Carousel>
                </div>

                <div className='swiper_arr'>

                    <h1>HOT DEAL</h1>

                    <MDBTabs className='mb-3'>
                        <MDBTabsItem>
                            <MDBTabsLink onClick={() => handleBasicClick('tab1')} active={basicActive === 'tab1'}>
                                Men
                            </MDBTabsLink>
                        </MDBTabsItem>
                        <MDBTabsItem>
                            <MDBTabsLink onClick={() => handleBasicClick('tab2')} active={basicActive === 'tab2'}>
                                Women
                            </MDBTabsLink>
                        </MDBTabsItem>
                        <MDBTabsItem>
                            <MDBTabsLink onClick={() => handleBasicClick('tab3')} active={basicActive === 'tab3'}>
                                Couple
                            </MDBTabsLink>
                        </MDBTabsItem>
                    </MDBTabs>

                    <MDBTabsContent>
                        <MDBTabsPane show={basicActive === 'tab1'}>
                            <Swiper
                                // slidesPerView={1}
                                scrollbar={{
                                    hide: true,
                                }}
                                breakpoints={{
                                    320: {
                                        slidesPerView: 1,
                                        spaceBetween: 20
                                    },
                                    501: {
                                        slidesPerView: 2,
                                        spaceBetween: 20
                                    },

                                    740: {
                                        slidesPerView: 3,
                                        spaceBetween: 20
                                    },

                                    1024: {
                                        slidesPerView: 4,
                                        spaceBetween: 20
                                    }
                                }}

                                modules={[Scrollbar]}
                                className="mySwiper3"
                            >
                                {
                                    props.mens.map((v, i) => {
                                        return (

                                            <SwiperSlide key={i}>
                                                <MDBNavbarLink onClick={() => {
                                                    navigate('/detail/men/' + i)
                                                }}>
                                                    <img src={props.mens[i].imgUrl}></img>
                                                    <p>{props.mens[i].title}</p>
                                                    <span>{props.mens[i].price}원</span>
                                                </MDBNavbarLink>
                                            </SwiperSlide>
                                        )
                                    })
                                }
                            </Swiper>

                        </MDBTabsPane>
                        <MDBTabsPane show={basicActive === 'tab2'}>
                            <Swiper
                                // slidesPerView={1}

                                scrollbar={{
                                    hide: true,
                                }}
                                breakpoints={{
                                    320: {
                                        slidesPerView: 1,
                                        spaceBetween: 20
                                    },
                                    501: {
                                        slidesPerView: 2,
                                        spaceBetween: 20
                                    },

                                    740: {
                                        slidesPerView: 3,
                                        spaceBetween: 20
                                    },

                                    1024: {
                                        slidesPerView: 4,
                                        spaceBetween: 20
                                    }
                                }}
                                modules={[Scrollbar]}
                                className="mySwiper4"
                            >
                                {
                                    props.womens.map((v, i) => {
                                        return (
                                            <SwiperSlide key={i}>
                                                <MDBNavbarLink onClick={() => {
                                                    navigate('/detail/women/' + i)
                                                }}>
                                                    <img src={props.womens[i].imgUrl}></img>
                                                    <p>{props.womens[i].title}</p>
                                                    <span>{props.womens[i].price}원</span>
                                                </MDBNavbarLink>
                                            </SwiperSlide>
                                        )
                                    })
                                }
                            </Swiper>
                        </MDBTabsPane>
                        <MDBTabsPane show={basicActive === 'tab3'}>
                            <Swiper
                                scrollbar={{
                                    hide: true,
                                }}

                                breakpoints={{
                                    320: {
                                        slidesPerView: 1,
                                        spaceBetween: 20
                                    },

                                    501: {
                                        slidesPerView: 2,
                                        spaceBetween: 20
                                    },


                                    740: {
                                        slidesPerView: 2,
                                        spaceBetween: 20
                                    },

                                    1024: {
                                        slidesPerView: 4,
                                        spaceBetween: 20
                                    }
                                }}
                                modules={[Scrollbar]}
                                className="mySwiper5"
                            >
                                {
                                    props.couples.map((v, i) => {
                                        return (
                                            <SwiperSlide key={i}>
                                                <MDBNavbarLink onClick={() => {
                                                    navigate('/detail/couple/' + i)
                                                }}>
                                                    <img src={props.couples[i].imgUrl}></img>
                                                    <p>{props.couples[i].title}</p>
                                                    <span>{props.couples[i].price}원</span>
                                                </MDBNavbarLink>
                                            </SwiperSlide>
                                        )
                                    })
                                }
                            </Swiper>
                        </MDBTabsPane>
                    </MDBTabsContent>
                </div>

                <div className='center_slide'>

                    <h1>23SS <br />
                        SWEAT COLLECTION
                    </h1>

                    <img src={process.env.PUBLIC_URL +"img/center_slide.jpg"}></img>
                    
                </div>

                <div className='couple'>
                    <h1>
                        23SS <br />
                        SWEAT COUPLE PAJAMAS
                    </h1>

                    <img src={process.env.PUBLIC_URL +"img/couple.jpg"}></img>
                </div>

                <div className='gimo'>
                    <h1>
                        23SS <br />
                        SWEAT GIMO PAJAMAS
                    </h1>

                    <div className='gimo_btn'>
                        <img src={process.env.PUBLIC_URL +"img/gimo.jpg"}></img>
                        <button>상품 보기</button>
                    </div>
                </div>

                <div className='all_pajamas'>

                    <h1>ALL PAJAMAS</h1>

                    <div className="all_men">
                        <img src={process.env.PUBLIC_URL +"img/3_men_gif.gif"} title='Men'></img>
                        <p>Men</p>
                    </div>

                    <div className="all_women">
                        <img src={process.env.PUBLIC_URL +"img/3_women_gif.gif"} title='Women'></img>
                        <p>Women</p>
                    </div>

                    <div className="all_couple">
                        <img src={process.env.PUBLIC_URL +"img/3_couple_gif.gif"} title='couple'></img>
                        <p>Couple</p>
                    </div>

                    <div className='instargram'>

                        <h1>INSTAGRAM</h1>
                        <Swiper
                            slidesPerView={"auto"}
                            scrollbar={{
                                hide: true,
                            }}
                            modules={[Scrollbar]}
                            className="mySwiper2"
                        >
                            {
                                instargrams.map((v, i) => {
                                    return (
                                        <SwiperSlide key={i}>
                                            <img src={instar[i].imgUrl}></img>
                                        </SwiperSlide>
                                    )
                                })
                            }
                        </Swiper>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Home;


    
    